import React, { useState } from "react";
import MainLayout from "../layouts/MainLayout";
import { findServerInfo, getServerList } from "../game-server";

const GAME_SERVERS = getServerList();

function GameServer() {
    const [gameServer, setGameServer] = useState(null);


    return (
        <MainLayout>
            GameServer

            <select className="form-control" onChange={event => setGameServer(event.target.value)}>
                {GAME_SERVERS.map((elem, idx) => <option key={idx}>
                    {elem}
                </option>)}
            </select>
        </MainLayout>
    )
}

export default GameServer;